___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --green: #589C57;
    --text: #303F2E;
    --white: #F0F0F0
}

*{
    font-family: 'Inknut Antiqua', serif;
}

html{
    height: 100%;
}

body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: var(--green);
}

header{
    position: fixed;
    top: 0;
    left: 0;
    height: 60px;
    z-index: 5;
} 

.color{
    background-color: var(--green);
    width: 100vw;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-bottom: 1px solid var(--white) ;
}

.navbar{
    width: calc(50vw - 150px);
    display: flex;
    gap: clamp(5px, 10%, 40px);
    justify-content: center;
    margin-right: 30px;
    z-index: 5;
}

.navbar li{
    font-size: 20px;
    list-style: none;
    color: var(--white);
    cursor: pointer;
    font-weight: lighter;
    transition: 600ms;
}

.navbar li:hover{
    font-weight: bolder;
}

.logo{
    width: 162px;
    position: absolute;
    left: calc(50vw - 95px);
    top: 25px;
    overflow: visible;
}

.logo .lettering, .logoImg{
    position: absolute;
    left: calc(50% - 45px);
    z-index: 5;
}

header .lettering {
    fill: none;
}

header .lettering text{
    fill: var(--white);
    font-size: 18px;
    text-align: center;
    font-weight: bolder;
}

.logo .lettering{
    overflow: visible;
    width: 120px;
    height: 100px;
    top: 0;
}

.logo .logoImg{
    height: 100px;
    top: 9px;
    fill: var(--white);
    left: calc(50% - 36px);
}

.logoImg *{
    fill: var(--white) !important;
    stroke: var(--white);
}

.blocker1, .blocker2, .blocker3{
    content: "";
    position: fixed;
    top: 0;
    z-index: 2;
    height: 61px;
    background-color: var(--green);
}

.blocker1{
    left: 0;
    width: clamp(5px, 10%, 50px);
}

.blocker2{
    right: 0;
    width: clamp(1px, 5%, 10%);
}

.blocker3{
    left: calc(50vw - 121px);
    width: 242px;
}`, "",{"version":3,"sources":["webpack://./src/headerStyles.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,eAAe;IACf;AACJ;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,8BAA8B;AAClC;;AAEA;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,YAAY;IACZ,UAAU;AACd;;AAEA;IACI,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,yBAAyB;IACzB,mBAAmB;IACnB,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,0BAA0B;IAC1B,uBAAuB;IACvB,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,eAAe;IACf,oBAAoB;IACpB,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,uBAAuB;IACvB,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,aAAa;IACb,MAAM;AACV;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,6BAA6B;IAC7B,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,eAAe;IACf,MAAM;IACN,UAAU;IACV,YAAY;IACZ,8BAA8B;AAClC;;AAEA;IACI,OAAO;IACP,4BAA4B;AAChC;;AAEA;IACI,QAAQ;IACR,0BAA0B;AAC9B;;AAEA;IACI,wBAAwB;IACxB,YAAY;AAChB","sourcesContent":[":root{\r\n    --green: #589C57;\r\n    --text: #303F2E;\r\n    --white: #F0F0F0\r\n}\r\n\r\n*{\r\n    font-family: 'Inknut Antiqua', serif;\r\n}\r\n\r\nhtml{\r\n    height: 100%;\r\n}\r\n\r\nbody{\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    background-color: var(--green);\r\n}\r\n\r\nheader{\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    height: 60px;\r\n    z-index: 5;\r\n} \r\n\r\n.color{\r\n    background-color: var(--green);\r\n    width: 100vw;\r\n    height: 100%;\r\n    display: flex;\r\n    justify-content: flex-end;\r\n    align-items: center;\r\n    border-bottom: 1px solid var(--white) ;\r\n}\r\n\r\n.navbar{\r\n    width: calc(50vw - 150px);\r\n    display: flex;\r\n    gap: clamp(5px, 10%, 40px);\r\n    justify-content: center;\r\n    margin-right: 30px;\r\n    z-index: 5;\r\n}\r\n\r\n.navbar li{\r\n    font-size