___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --green: #589C57;
    --text: #303F2E;
    --white: #F0F0F0
}

#home{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}

#greetings1, #greetings2{
    text-align: center;
    font-size: 80px;
    color: var(--text);
    font-weight: bolder;
    width: 100%;
    position: inherit;
    line-height: 1;
}

#greetings1{
    top: 16vh;
    z-index: 1;
}

#greetings2{
    top: 32vh;
    z-index: 5;
}

#cerve1, #cerve2{
    height: 460px;
    z-index: 2;
    position: absolute;
}

#cerve1{
    left: calc(50vw - 410px);
    transform: rotate(15deg);
    top: 27vh;
    animation: 2s linear cerve1;
}

#cerve2{
    right: calc(50vw - 410px);
    transform: rotate(-15deg);
    top: 28vh;
    animation: 2s linear cerve2;
}

#info{
    display: flex;
    position: absolute;
    bottom: 20px;
    width: 100%;
    margin-right: clamp(1px, 5%, 10%);;
    right: 0;
    justify-content: flex-end;
    gap: 15px;
}

.info-block{
    display: flex;
    align-items:center;
    gap: 15px;
}

.info-icon *{
    fill: var(--text);
    stroke: none;
}

#clock{
    height: 45px;
    position: relative;
    top: 7px;
    left: 5px;
    overflow: visible;
}

#sign{
    height: 45px;
    position: relative;
    left: 5px;
    top: 0px;
}

.info-text{
    color: var(--text);
    font-size: 13px;
    line-height: 1.5;
    font-weight: 100;
    margin: 0;
}

#sticker{
    position: absolute;
    top: 73%;
    left: clamp(20px, 8%, 100px);
    height: 150px;
    width: 150px;
    z-index: 1;
    cursor: pointer;   
}

#sticker #letterDonut{
    position: absolute;
    overflow:visible;
    width: 150px;
    top: 0;
    left: 0;
    transform-origin: 75px 75px;
    animation: 10s linear infinite donut;
}

#sticker #letterDonut .stickerText{
    fill: var(--text);
    font-size: 15px;
}

#iconBeer{
    height: 140px;
    position: absolute;
    left: 14%;
    top: 10%;
    fill: var(--text);
}

#iconBeer:hover{
    animation: 0.5s linear infinite beerIcon;
}

#iconBeer *{
    fill: var(--text);
}

#callToAction{
    position: absolute;
    right: -90px;
    height: 150px;
    width: 150px;
    top: 60vh;
    border-radius: 600px;
    background-color: var(--text);
    z-index: 6;
    transition: 300ms;
    cursor: pointer;
}

#callToAction:hover{
    background-color: var(--white);
    color: var(--text);
}

#callToAction:hover::after{
    content: "Go";
    color: var(--text);
    position: absolute;
    font-size: 20px;
    top: 34%;
    left: 10%;
}

#callToAction #reservation{
    overflow:visible;
    position: absolute;
    transform: rotate(-47deg);
    top: -57px;
    left: 10px;
}

#callToAction #reservation .actionText{
    fill: var(--white);
    font-weight: 100;
}

.cut{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    z-index: 0;
}`, "",{"version":3,"sources":["webpack://./src/homeStyles.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,eAAe;IACf;AACJ;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,MAAM;IACN,OAAO;AACX;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,kBAAkB;IAClB,mBAAmB;IACnB,WAAW;IACX,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,aAAa;IACb,UAAU;IACV,kBAAkB;AACtB;;AAEA;IACI,wBAAwB;IACxB,wBAAwB;IACxB,SAAS;IACT,2BAA2B;AAC/B;;AAEA;IACI,yBAAyB;IACzB,yBAAyB;IACzB,SAAS;IACT,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,iCAAiC;IACjC,QAAQ;IACR,yBAAyB;IACzB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,SAAS;IACT,QAAQ;AACZ;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,4BAA4B;IAC5B,aAAa;IACb,YAAY;IACZ,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,YAAY;IACZ,MAAM;IACN,OAAO;IACP,2BAA2B;IAC3B,oCAAoC;AACxC;;AAEA;IACI,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,SAAS;IACT,QAAQ;IACR,iBAAiB;AACrB;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,SAAS;IACT,oBAAoB;IACpB,6BAA6B;IAC7B,UAAU;IACV,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,8BAA8B;IAC9B,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,QAAQ;IACR,SAAS;AACb;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,yBAAyB;IACzB,UAAU;IACV,UAAU;AACd;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,YAAY;IACZ,WAAW;IACX,gBAAgB;IAChB,UAAU;AACd","sourcesContent":[":root{\r\n    --green: #589C57;\r\n    --text: #303F2E;\r\n    --white: #F0F0F0\r\n}\r\n\r\n#home{\r\n    position: absolute;\r\n    height: 100%;\r\n    width: 100%;\r\n    top: 0;\r\n    left: 0;\r\n}\r\n\r\n#greetings1, #greetings2{\r\n    text-align: center;\r\n    font-size: 80px;\r\n    color: var(--text);\r\n    font-weight: bol