___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --green: #589C57;
    --text: #303F2E;
    --white: #F0F0F0;
    --lightGreen: #75B36B;
    --blur: #303f2ec4;
}

*{
    color: var(--text);
    box-sizing: border-box;
}

#menu{
    margin-top: 80px; 
}

.title2{
    display: inline-block;
    width: 100%;   
    font-size: 3.7rem;
    text-align: center;
    margin: 0;
    margin-top: 50px;
    position: relative;
}

.separation{
    width: 470px;
    margin-top: -15px;
}

.productGrid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 40px;
}

.productContainer{
    aspect-ratio: 1/1;
    width: 100%;
    background-color: red;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.productContainer:hover .message{
    display: inline-block;
}

.productContainer:hover .productImg{
    max-height: 90%;
}

.productImg{
    max-height: 70%;
    transition: 300ms;
}


.message{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--blur);
    display: none;
}

.productText,
b{
    line-height: 1.6rem;
    color: var(--white);
    font-size: 0.8rem;
}

b{
    font-weight:900;
    font-size: 1.2rem;
    line-height: 2rem;
}

.productText{
    position: absolute;
    top: 0;
    margin: 0;
    padding: 40px;
    width: 100%;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    font-weight: 100;
}

.light{
    background-color: var(--lightGreen);
}

.dark{
    background-color: var(--green);
